import "core-js/modules/es.array.push.js";
import bottomNav from "@/components/bottomNav.vue";
import { reactive, toRefs } from 'vue';
import { getData, getData2, getmode } from "../assets/api/home";
import { useRouter } from 'vue-router';
import { ref } from 'vue';
export default {
  components: {
    bottomNav
  },
  setup() {
    let router = useRouter();
    let teacherhead = ref([]);
    let banner = reactive({
      // 轮播图
      bannerList: null,
      // 轮播图下面的tab
      menuList: null,
      // tab下面的动图
      tabImg: null,
      // 精选课程
      hotClass: [],
      hotClass2: null,
      index: 0,
      hotdata: []
    });
    let getDataFun = () => {
      getData().then(data => {
        // 轮播图
        banner.bannerList = data.data.results.body.sybnList;
        // 轮播图下面的tab
        banner.menuList = data.data.results.body.menuList;
        // 精选课程
        // banner.hotClass = data.data.results.body.netList
        banner.hotdata = banner.hotClass.slice(0, 6);
        // tab下面的动图
        banner.tabImg = data.data.results.body.syzwList;
        // console.log(data,1111);

        // banner.hotClass.forEach(item=>{
        //    let someteacher= item.teacherImg.split(",")
        //     teacherhead.value.push(someteacher)
        //     console.log(teacherhead.value);
        // })
      });

      getData2().then(res => {
        banner.hotClass2 = res.data.results.list;
        console.log(res.data.results.list);
      });
      getmode("", "1").then(res => {
        console.log(res);
        banner.hotClass = res.data.results.list;
        // ac.dataList = res.data.results.list
        // console.log(ac.dataList);
      });
    };

    getDataFun();
    let navigate = id => {
      console.log(id);
      router.push({
        path: "/netshop",
        query: {
          key: id
        }
      });
      console.log(router);
    };

    //教师头像
    let tomakehead = () => {
      // banner.hotClass.forEach(item=>{
      //     console.log(item.teacherImg,22222222);
      // })
    };
    tomakehead();

    // 下拉刷新
    const loading = ref(false);
    const finished = ref(false);
    const onLoad = () => {
      setTimeout(() => {
        for (let i = 0; i < 2; i++) {
          banner.index += 2;
          banner.hotdata = banner.hotClass.slice(0, banner.index);
        }
        loading.value = false;
        if (banner.hotdata.length >= 17) {
          finished.value = true;
        }
      }, 1000);
    };
    let goclass = title => {
      router.push({
        path: "/classlesson",
        query: {
          "title": title
        }
      });
    };
    return {
      // 展开banner的内容
      ...toRefs(banner),
      navigate,
      onLoad,
      loading,
      finished,
      goclass,
      teacherhead
    };
  }
};